const filterByCondition = (filters, data) => {
  let result = [];
  let keys = Object.keys(filters);
  result = data.filter((alarm) => {
    if (keys && keys.length > 0) {
      return keys.every((key) => {
        if (alarm.hasOwnProperty(key)) {
          let list = filters[key];
          let value = '' + alarm[key];
          if (list.length == 0) {
            return true;
          }
          if ((list && list.includes(value)) || key == value) {
            return true;
          }
          return false;
        }
      });
    } else {
      return true;
    }
  });
  return result;
};
const filterByColumn = (columns) => {
  let result = columns.filter((item) => {
    if (item.hasOwnProperty('show')) {
      return item.show;
    } else {
      return true;
    }
  });
  return result;
}
const convertColumnToFilter = (config, columns, isSet) => {
  let result = [];
  let selecteds = [];
  let cAc = [];
  columns.forEach((item) => {
    let obj = {
      name: item.title,
      value: item.code,
    }
    result.push(obj);
    if (config.indexOf(item.code) != -1 || !isSet) {
      selecteds.push(item.code);
      cAc.push(item);
    }
  });
  return {
    configColumns: result,
    columns: cAc,
    selecteds
  }

}
